<template>
  <div class="bs-message">
    <el-table :data="messageList" style="width: 100%" max-height="250">
      <el-table-column fixed prop="create_time" label="消息时间" width="200"></el-table-column>
      <el-table-column prop="send_user_name" label="姓名" width="150"></el-table-column>
      <el-table-column prop="send_user_role" label="权限" width="200"></el-table-column>
      <el-table-column prop="state" label="状态" width="120"></el-table-column>
      <el-table-column prop="type" label="消息类别" width="800"></el-table-column>
      <el-table-column fixed="right" label="操作" width="380">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="deleteRow(scope.$index, managerList)"
            type="text"
            size="small"
          >移除</el-button>
          <el-button
            @click.native.prevent="seeMessage(scope.$index, managerList)"
            type="text"
            size="small"
          >查看</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import * as ajax from "@/api";

export default {
  created() {
    ajax.getMessageList().then(resp => {
      if (resp.data.code) {
        this.messageList = resp.data.data.message_list;
      }
    })
  },
  data(){
      return{
          messageList:[]
      }
  },
  methods:{
    seeMessage(index,list){
    }
  }
};
</script>

<style lang='scss'>
.bs-message {
  flex: 1;
  .el-table {
    .cell {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .el-table__body-wrapper {
      overflow-y: hidden;
    }
    max-height: 100% !important;
    .el-table__body-wrapper {
      max-height: 100% !important;
    }
  }
  .el-table__fixed-body-wrapper{
    max-height: 100% !important;
  }
}
</style>